.item {
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
}

.item:hover {
    scale: 1.1;
    transition: 0.3s;
}
  
.item img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-bottom: 5px;
}

.item span {
    color: white;
}